}

html {
  max-width: 768px;
  margin-left: auto;
  margin-right: auto;
  font-size: 12px;
  line-height: 1.5;
  font-family: "Hiragino Sans GB", sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: .5rem;
}

p {
  margin-top: 0;
  // margin-bottom: 14px;
  margin-bottom: 1rem;
}

img {
  vertical-align: middle;
}

@font-face {
  font-family: 'Apple Chancery';
  src: url('../fonts/apple-chancery.eot');
  src: url('../fonts/apple-chancery.eot?#iefix') format('embedded-opentype'),
       url('../fonts/apple-chancery.woff2') format('woff2'),
       url('../fonts/apple-chancery.woff') format('woff'),
       url('../fonts/apple-chancery.ttf') format('truetype');
}

.container {
  margin-left: 10px;
  margin-right: 10px;
}

#skrollr-body {
  /*border-left: 1px solid red;
  border-right: 1px solid red;*/
}
#skrollr-body:before,
#skrollr-body:after {
  content: "";
  display: table;
}

#skrollr-body:after {
  clear: both;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

#loading {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #363434;
  .loading-indicator {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 30px;
    height: 30px;
    margin: auto;
    border-radius: 9999px;
    background-color: #fff;
    animation: loading-indicator .3s infinite alternate ease-out both;

  }
  &.loaded-animation {
    .loading-indicator {
      animation: loaded-indicator .4s ease-out both;
    }
  }
}

#skrollr-body.loaded-animation {
  animation: body-loaded 1.2s ease-out backwards;
  // .gallery-cover {
  //   animation: gallery-cover .5s .4s ease-out backwards;
  // }
  //
  // @for $i from 1 through 10 {
  //   .gallery-author:nth-child(#{$i}) {
  //     z-index: 100;
  //     animation: gallery-author .65s #{.4 + .1*$i}s ease-out both;
  //   }
  // }
  //
  // .photo-group-container .photo-group:nth-child(1) {
  //   animation: group-first .6s ease-out both;
  // }

}

@keyframes group-first {
  0% {
    transform: translate3d(30px, -400px, 0) rotateZ(20deg);
    opacity: 0;
  }
  100% {
    transform: rotate(0) translate3d(0,0,0);
    opacity: 1;
  }
}

@keyframes gallery-author {
  0% {
    transform: translate3d(-10px, -600px, 0) rotate(-720deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate(0);
  }
}

@keyframes gallery-cover {
  0% {
    transform: rotateZ(-10deg) translate3d(-10px, -400px, 0);
    opacity: 0;
  }
  100% {
    transform: rotate(0) translate3d(0,0,0);
    opacity: 1;
  }
}

@keyframes loading-indicator {
  0% {
    transform: scale3d(1, 1) translateZ(0);
  }
  100% {
    transform: scale(.5, .5) translateZ(0);
  }
}

@keyframes loaded-indicator {
  100% {
    transform: scale(30, 30) translateZ(0);
  }
}

@keyframes body-loaded {
  0% { opacity: 0; transform: translate3d(0, 40px, 0);}
  100% { opacity: 1; transform: translate3d(0, 0, 0);}
}



$enable-parallax: true;

$photo-group-left-right-gap-width: 20px;
.photo-group-container {
  margin-left: $photo-group-left-right-gap-width;
  margin-right: $photo-group-left-right-gap-width;
}

.photo-group {
  position: relative;
  // margin-bottom: 10px;
  @if $enable-parallax {
    overflow: hidden;
  }
}

.photo-group-inner {

}

.photo-item {
  // -webkit-box-sizing: border-box;
  //    -moz-box-sizing: border-box;
  //         box-sizing: border-box;
  position: absolute;
  transform: translateZ(0);
  /*outline: 1px solid olive;*/
  @if $enable-parallax {
    overflow: hidden;
  }
}

.photo-item-inner {
  // -webkit-transform: scale(1.1, 1.1);
  // transform: scale(1.1, 1.1);
  position: relative;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}

.photo-item:before,
.photo-item:after,
.photo-item-inner:before,
.photo-item-inner:after {
  content: '';
  position: absolute;
  z-index: 1;
  background: #fff;
  transform: translateZ(0);
}

.photo-item-wrapper {
  display: block;
  height: 0;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.photo-item img {
  display: block;
  width: 100%;
  height: auto;
  /*tranform: scale(1.1,1.1);*/
}

.photo-info {
  position: relative;
  // padding-left: 20px;
  // padding-right: 20px;
  // margin-top: 10px;
  // margin-bottom: 10px;
}

.photo-author {
  position: absolute;
  top: 6px;
  font-size: 10px;
  line-height: 1;
}

.photo-author .author-name {
  color: #8e915c;
}

.photo-description {
  font-size: 11px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  // height: 100px;
  padding-left: 20px;
  padding-right: 20px;
  // padding-bottom: 34px;
  // padding-top: 45px;
  height: 135px;
}

$photo-item-gap-width: 5px;
$photo-item-gap: $photo-item-gap-width solid #fff;
// 左上z-index:1 border-right border-bottom 尺寸加上 border-right border-bottom
// 右上z-index:2 border-left botder-bottom  尺寸加上 border-bottom
// 左下z-index:3 border-top border-right    尺寸加上 border-right
// 右下z-index:4 border-top border-left
// 最后一排的尺寸不加上 border-bottom

@mixin photo-item-border-left {
  height: 100%;
  width: $photo-item-gap-width;
  top: 0;
  left: -$photo-item-gap-width/2;
}
@mixin photo-item-border-right {
  height: 100%;
  width: $photo-item-gap-width;
  top: 0;
  right: -$photo-item-gap-width/2;
}
@mixin photo-item-border-top {
  width: 100%;
  height: $photo-item-gap-width;
  left: 0;
  top: -$photo-item-gap-width/2;
}
@mixin photo-item-border-bottom {
  width: 100%;
  height: $photo-item-gap-width;
  left: 0;
  bottom: -$photo-item-gap-width/2;
}

@mixin photo-item-border ($top: false, $right: false, $bottom: false, $left: false) {
  @if $top { border-top: $photo-item-gap; }
  @if $right { border-right: $photo-item-gap; }
  @if $bottom { border-bottom: $photo-item-gap; }
  @if $left { border-left: $photo-item-gap; }
}

