  .photo-item:nth-child(3) {
    z-index: 3;
    bottom: 0;
    left: 33.33%;
    width: 33.33%;
    @include photo-item-border(true, true, false, true);
    .photo-item-wrapper {
      padding-bottom: percentage(157px/186px);
    }
  }
  .photo-item:nth-child(4) {
    z-index: 4;
    right: 0;
    bottom: 0;
    width: 33.33%;
    @include photo-item-border(true, false, false, true);
    .photo-item-inner:before {
      @include photo-item-border-top;
    }
    .photo-item-inner:after {
      @include photo-item-border-left;
    }
    .photo-item-wrapper {
      padding-bottom: percentage(157px/186px);
    }
  }
}




