  /*border-left: 1px solid red;
  border-right: 1px solid red;*/
}

#skrollr-body:before,
#skrollr-body:after {
  content: "";
  display: table;
}

#skrollr-body:after {
  clear: both;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

#loading {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #363434;
}

#loading .loading-indicator {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 30px;
  height: 30px;
  margin: auto;
  border-radius: 9999px;
  background-color: #fff;
  animation: loading-indicator 0.3s infinite alternate ease-out both;
}

#loading.loaded-animation .loading-indicator {
  animation: loaded-indicator 0.4s ease-out both;
}

#skrollr-body.loaded-animation {
  animation: body-loaded 1.2s ease-out backwards;
}

@keyframes group-first {
  0% {
    transform: translate3d(30px, -400px, 0) rotateZ(20deg);
    opacity: 0;
  }
  100% {
    transform: rotate(0) translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes gallery-author {
  0% {
    transform: translate3d(-10px, -600px, 0) rotate(-720deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate(0);
  }
}

@keyframes gallery-cover {
  0% {
    transform: rotateZ(-10deg) translate3d(-10px, -400px, 0);
    opacity: 0;
  }
  100% {
    transform: rotate(0) translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes loading-indicator {
  0% {
    transform: scale3d(1, 1) translateZ(0);
  }
  100% {
    transform: scale(0.5, 0.5) translateZ(0);
  }
}

@keyframes loaded-indicator {
  100% {
    transform: scale(30, 30) translateZ(0);
  }
}

@keyframes body-loaded {
  0% {
    opacity: 0;
    transform: translate3d(0, 40px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

