.photo-group {
  position: relative;
  overflow: hidden;
}

.photo-item {
  position: absolute;
  transform: translateZ(0);
  /*outline: 1px solid olive;*/
  overflow: hidden;
}

.photo-item-inner {
  position: relative;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}

.photo-item:before,
.photo-item:after,
.photo-item-inner:before,
.photo-item-inner:after {
  content: '';
  position: absolute;
  z-index: 1;
  background: #fff;
  transform: translateZ(0);
}

.photo-item-wrapper {
  display: block;
  height: 0;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

